import { CHANGE_AREA,
    FETCH_LIST_REQUEST, FETCH_LIST_SUCCESS, FETCH_LIST_FAIL } from './type'

const initialState = {
    items: [],
    loading: true,
    err: null,
    areaList: [
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
        {key:'',name:''},
    ],
    selectedArea: ''
}

const housesReducer = (state=initialState, action) => {
    switch(action.type) {
        case CHANGE_AREA:
            return {
                ...state,
                selectedArea: action.payload
            }
        case FETCH_LIST_REQUEST:
            return {
                ...state,
                loading: true
            }
        case FETCH_LIST_SUCCESS:
            return {
                ...state,
                items: action.payload,
                loading: false
            }
        case FETCH_LIST_FAIL:
            return {
                ...state,
                err: action.payload,
                loading: false
            }
        default: return state;
    }
}

export default housesReducer;